angular.module('hsGraph').factory('HsLegend', ['HsComponent', 'HsMarkers', 'HsLabels', function HsLegendFactory(HsComponent, HsMarkers, HsLabels) { 
    function setLegendRect(obj, numSeries, width) {
        var oc = obj.cfg();
        var p = oc.padding,
            m = oc.margins;
        var w = width+p.left+p.right,                            // px
            h= oc.height*oc.lineSpace*numSeries+p.top+p.bottom;  // em
        var dx = -w*oc.x/100, dy = -h*oc.y/100;
        oc.elem.transition().duration(oc.transition)
            .attr("x", (oc.x*(100-m.right)/100+(100-oc.x)*m.left/100)+'%')
            .attr("y", (oc.y*(100-m.bottom)/100+(100-oc.y)*m.top/100)+'%');
        oc.elem.select('svg').transition().duration(oc.transition)
            .attr("x", dx+'px').attr("y", dy+'em')
            .attr('height', h+'em')
            .attr('width',  w+'px');
    }
    
	return function(elem) {
		var obj = new HsComponent('legend', elem, 'Legend').cfg({
			x: 0,  y: 0,                // position of legend rect
            padding:{
                left:5, right:5,        // px, 
                top:0.3, bottom:0.3     // em 
            },
			lineWidth:   50,            // px, length of line
			lineSpace:   1.5,           // em, spacing between lines
			height:      1              // em legend height as multiple of series count
		});
        obj.cfg().labelPos =  (obj.cfg().padding.left + (obj.cfg().lineWidth-obj.cfg().padding.left)/2) + 'px';

        var p = obj.cfg().elem.select('svg');
		obj.lineGroup 	= p.append('svg').attr('class', 'hs-legend-line-group')
			.attr('x', '0%').attr('y', '0%').attr('width', '100%').attr('height', '100%');
		obj.markerGroup = p.append('svg').attr('class', 'hs-legend-marker-group')
			.attr('x', '0%').attr('y', '0%').attr('width', '100%').attr('height', '100%');
		obj.labelGroup 	= p.append('svg').attr('class', 'hs-legend-label-group')
			.attr('x', '0%').attr('y', '0%').attr('width', '100%').attr('height', '100%');

        obj.labels = new HsLabels(obj.labelGroup, "Legend Labels", obj).cfg({
            cssClass: "hs-legend-label",
            anchor: 'left',
            baseline: 'auto',
            dx: '6px', dy: '0.2em'
        });
        
        obj.markers = new HsMarkers(obj.markerGroup, "Legend Markers", obj).cfg({
            cssClass: 'hs-legend-marker'
        }); 

        obj.init = function() {
        };

		obj.setSize = function init(margins, series) {
		    var i;       
		    obj.cfg().margins = margins;
		    setLegendRect(obj, series.length, +obj.cfg().width);
		    obj.legendItems = [];
		    obj.legendItems.names = {};
		    if (obj.cfg().labels) { // if labels explicitely set in hs-legend element
                for (i=0; i<obj.cfg().labels.length; i++) { 
                    if (obj.cfg().labels[i].length > 0) {  // defined:
                        if (obj.legendItems.names[obj.cfg().labels[i]] === undefined) { // and unique:
                        	obj.legendItems.push({series:series[i], name:obj.cfg().labels[i]});
                        	obj.legendItems.names[obj.cfg().labels[i]] = true;
                        }
                    }
                }
		    } else {  // else construct labels from series
    		    for (i=0; i<series.length; i++) { 
    		        var s = series[i];
    		        if (s.keys.colors === "") {
        		        if (obj.legendItems.names[s.cfg().compName] === undefined) {
        		        	obj.legendItems.push({series:s, name:s.cfg().compName});
                        	obj.legendItems.names[s.cfg().compName] = true;
        		        }
                    } else {
                        var colors = s.data.colDomain(s.keys.colors);
                        for (var j=0; j<colors.length; j++) {
                            obj.legendItems.push({series:s, name:colors[j], markerCssClass:colors[j].trim().replace(' ', '_') });                        
                        }
                    } 
    		    }
		    }
		};

		obj.draw = function draw() {
            function ypos(d, i) { return ((i+0.5)*obj.cfg().lineSpace) + obj.cfg().padding.top + 'em'; }

			var lines  = obj.lineGroup.selectAll('.hs-legend-line').data(obj.legendItems);
			lines.enter().append('svg')
			    .each(function(d) { d3.select(this).attr('class', d.series.cfg().elem.attr('class')); })
			    .append('line').attr('class', 'hs-legend-line')
				.attr('x1', obj.cfg().padding.left+'px').attr('x2', obj.cfg().lineWidth+'px')
				.attr('y1', ypos).attr('y2', ypos);
			lines.exit().remove();

			// draw markers
			obj.markers.draw(obj.legendItems, {xpos:obj.cfg().labelPos, ypos:ypos}, '', function(d) { 
			    var r = d.series.markers.cfg().size;
			    return d.markerCssClass? { 
			        cssClass2: 'hs-legend-marker ' + d.markerCssClass,
			        cssClass: d.series.cfg().elem.attr('class'),
                    style: 'rect',
                    size:  [2*r, r],
                    unit: '%'
                } : {
                    cssClass2: 'hs-legend-marker',
                    cssClass: d.series.cfg().elem.attr('class'),
                    style: d.series.markers.cfg().style,
                    size:  d.series.markers.cfg().size
                };
			});

            // draw labels
            obj.labels.strings = obj.legendItems;
            var sizeRect = obj.labels.draw([obj.cfg().lineWidth+'px', ypos], 'ordinal', function(d) { return d.name; });
            setLegendRect(obj, obj.legendItems.length, +obj.cfg().lineWidth + sizeRect.right); 
		};

		return obj;
	};
}]);

