angular.module('hsGraph').factory('HsVBarSeries', ['HsSeries', 'hsGraphUtil', '$compile', function(HsSeries, util, $compile) {
    "use strict";    
    function collectBarSet(obj) {
        return function(series) {
            // determine series set data
            if (!obj.set) { 
                obj.set = { idOfType:-1, seriesOfType:0 };
                obj.set.type = obj.cfg('seriesType');
                for (var i=0; i<series.length; i++) {
                    if (obj.set.type === series[i].cfg('seriesType')) {
                        if (series[i].set && series[i].set.idOfType<0) { obj.set.idOfType = obj.set.seriesOfType; }
                        obj.set.seriesOfType++;
                    }
                }
            }
        };
    }
    
    function drawMarkers(obj) {
        return function(coord) {
            var x = obj.data.indices.x, y = obj.data.indices.y, y2 = obj.data.indices.y2;
            var band = Math.max(obj.xaxis.getRangeBand(), 10);
            var numSeries = obj.set.seriesOfType;
            var s = obj.set.idOfType;
            function xpos(d)  { return coord.x.getRaw(d[x]) + s*util.correct(band)/numSeries + '%'; }
            function xposStacked(d)  { return coord.x.getRaw(d[x]) + '%'; }
            function ypos(d)  { return coord.y.get(d[y], d.y0) + '%'; }
            function yposStacked(d, i) { return (coord.y.get(aux[i].y, aux[i].y0)) + '%';}
            function width() { return util.correct(band)/numSeries+'%'; }
            function widthStacked() { return util.correct(band)+'%'; }
            function height(d) { return (coord.y.get(y2?d[y2]:0)-coord.y.get(d[y])) + '%'; }
            function tooltip(d) { return util.getTooltip(obj, d); }
            if (obj.cfg().stacked) {
            	/* jshint -W003 */
                var aux = obj.data.aux;  
            	obj.markers.draw(obj.data(), {xpos:xposStacked, ypos:yposStacked, width:widthStacked, height:height}, tooltip);
            } else {
            	obj.markers.draw(obj.data(), {xpos:xpos, ypos:ypos, width:width, height:height}, tooltip);
            }	
            obj.markerGroup.gradient(obj.markerGroup.selectAll('.hs-series-marker'), (obj.cfg().compName || '') + 'VBarGrd');
            $compile($(obj.markerGroup[0]))(this.myScope);  
        };
    }

    return function(elem) {
        var obj = new HsSeries(elem, 'vbar', 'Vertical Bar Series');
        obj.cfg().lines.visible = false;
        obj.cfg().area.visible = false;

        obj.drawMarkers   = drawMarkers(obj);    
        obj.collectBarSet = collectBarSet(obj);    
        return obj;
    };
}]);

