angular.module('hsGraph').directive('hsAxis', ['HsXAxis', 'HsYAxis', 'hsGraphUtil', 'hsDataType', function(HsXAxis, HsYAxis, util, dataType) {
	return {
		restrict: 'EA',
		replace: true,
		require: '^hsGraph',
		template: '<svg class="hs-axis"></svg>',
		link: function(scope, ele, attrs, ctrl) {
//            var outer = $(ele).outerHTML;
//            var inner = $(ele).innerHTML; 
//            console.log(outer, inner);
			var primary = (attrs.secondary)? false : true;
			
			// fetch dir
			var x = true;
			if (attrs.dir)         { x = attrs.dir === 'x'; }
			else if (attrs.y==="") { x = false; }
			var Axis = x? HsXAxis : HsYAxis;
			var comp = new Axis(util.d3.select(ele[0]), primary);
			
			// fetch scale
			var cfg = comp.cfg();
			cfg.scaleType = 'linear';
			if (attrs.scale) { cfg.scaleType = attrs.scale; }
            else if (attrs.linear === "")  	{ cfg.scaleType = 'linear'; }
            else if (attrs.log === "")     	{ cfg.scaleType = 'log'; }
            else if (attrs.date === "")    	{ cfg.scaleType = 'date'; }
            else if (attrs.index === "")   	{ cfg.scaleType = 'index'; }
            else if (attrs.ordinal === "") 	{ cfg.scaleType = 'ordinal'; }
            else if (attrs.nominal === "") 	{ cfg.scaleType = 'nominal'; }
            else if (attrs.percent === "") 	{ cfg.scaleType = 'percent'; }
			
			// fetch title
			if (attrs.title) { cfg.title = attrs.title; }
            var w = cfg.tickWidth,
                m = cfg.tickMargin;
            if (attrs.rotate){ 
                comp.axisTitle.cfg({
                    rotate: attrs.rotate,
                    anchor  : 'end',    
                    baseline: 'auto',
                    x       : x? ('95%') : (primary?'100%':'5%'),
                    y       : x? '100%' : '5%',
                    dx      : x? '0' : ('-'+(w+m)+'px'), 
                    dy      : x? '-4px' : '1em'
                });
            } else { 
                comp.axisTitle.cfg({
                    rotate  : '0', 
                    anchor  : x?'end':'end', 
                    baseline: 'auto',   // vertical align:   auto / middle / text-before-edge
                    x       : x? '95%':'100%',
                    y       : x? '80%' : '5%',
                    dx      : x? '0' : ('-'+(w+m)+'px'), 
                    dy      : x? '0' : '-4px'
                });
            }
            
            // fetch domain
			if (attrs.domain) {
				var defDomain = JSON.parse(attrs.domain);
				if (cfg.scaleType === 'date') {
					if (defDomain[0] !== 'auto') { defDomain[0] = dataType.toDate(defDomain[0]); }
					if (defDomain[1] !== 'auto') { defDomain[1] = dataType.toDate(defDomain[1]); }
				}
				cfg.defDomain = defDomain;
			}
			
			// done; add component
			ctrl.addComponent(comp);
		}
	};
}]);

