angular.module('hsGraph').factory('HsLabels', ['HsComponent', 'hsGraphUtil', function HsLabelFactory(HsComponent, util) {
    "use strict";    
    function rotate(size, alpha) {
        var l = size.left, r = size.right, b = size.bottom, t = size.top;
        var cos = Math.cos(alpha);
        var sin = Math.sin(alpha);
        var bl = [cos*l+sin*b, -sin*l+cos*b],
            tl = [cos*l+sin*t, -sin*l+cos*t],
            br = [cos*r+sin*b, -sin*r+cos*b],
            tr = [cos*r+sin*t, -sin*r+cos*t];
        size.bottom = Math.min(bl[1], tl[1], br[1], tr[1]);
        size.top    = Math.max(bl[1], tl[1], br[1], tr[1]);
        size.left   = Math.min(bl[0], tl[0], br[0], tr[0]);
        size.right  = Math.max(bl[0], tl[0], br[0], tr[0]);
    }

	return function(elem, name, parent) {
		var obj = new HsComponent('labels', elem, name, parent).cfg({
			cssClass:	'',
			x:	'0%',	y:	'0%',
			dx:	'0em',	dy:	'0em',
			rotate:		0,
			anchor 	: 'end',	// horizontal align: start / middle / end
			baseline: 'auto'    // vertical align:   auto / middle / text-before-edge
		});
		
		function setText(size, selection, textFn) {
			var r = selection.select('rect');
			var t = selection.select('text');
			t.text(textFn);
			t.each(function(d, i) {
				var _box;
				try { _box = t[0][i].getBBox(); }
				catch(err) {
					if (t[0][i].innerHTML.length > 0) {
						_box =  {
							x:t[0][i].clientLeft,
							y:t[0][i].clientTop,
							width:t[0][i].clientWidth,
							height:t[0][i].clientHeight
						};
					} else { _box = {x: 0, y: 0, width:0, height:0 }; }
				}
				size.top = Math.min(size.top, _box.y);
				size.bottom = Math.max(size.bottom, _box.y+_box.height);
				size.left = Math.min(size.left, _box.x);
				size.right = Math.max(size.right, _box.x+_box.width);
				r[0][i].setAttribute('x', _box.x);
				r[0][i].setAttribute('y', _box.y);
				r[0][i].setAttribute('width', _box.width);
				r[0][i].setAttribute('height', _box.height);
			});
		}

		function addLabel(selection, pos, cfg) {
			if (!cfg.visible) { return; }
			var s, r, t;
			s = selection.append('svg')
				.attr('class', cfg.cssClass)
				.attr('overflow', 'visible')
				.attr('x', pos[0]).attr('y', pos[1]);
			r = s.append('rect')
				.attr('transform', 'rotate('+cfg.rotate+')');
			t = s.append('text')
			    .attr('dx', cfg.dx).attr('dy', cfg.dy)
				.attr('transform', 'rotate('+cfg.rotate+')')
			    .attr("text-anchor", function(d, i) { 
			        if (!cfg.limitLabel) { return cfg.anchor; }
			        else {
			            if (i===obj.strings.length-1) { return 'end'; }
			            return cfg.anchor; 
			        }
			     })
			    .attr("alignment-baseline", cfg.baseline)	// required by Webkit
			    .attr("dominant-baseline", cfg.baseline);	// required by Firefox
		}

		function addLinkLabel(selection, pos, cfg, link) {
			if (!cfg.visible) { return; }
			var s, r, t, l;
			s = selection.append('svg')
				.attr('class', cfg.cssClass)
				.attr('overflow', 'visible')
				.attr('x', pos[0]).attr('y', pos[1]);
			r = s.append('rect')
				.attr('transform', 'rotate('+cfg.rotate+')');
			l = s.append('a')
				.attr('xlink:href', link);
			t = l.append('text')
			    .attr('dx', cfg.dx).attr('dy', cfg.dy)
				.attr('transform', 'rotate('+cfg.rotate+')')
			    .attr("text-anchor", function(d, i) { 
			        if (!cfg.limitLabel) { return cfg.anchor; }
			        else {
			            if (i===obj.strings.length-1) { return 'end'; }
			            return cfg.anchor; 
			        }
			     })
			    .attr("alignment-baseline", cfg.baseline)	// required by Webkit
			    .attr("dominant-baseline", cfg.baseline);	// required by Firefox
		}

		obj.strings = [];

		/**
		 * @param {[number] | [function]} pos array of x/y positions, each of which is either a value or a function
		 * @param {string} type the format type as defined in {@link hsGraph.hsGraphUtil.format hsGraphUtil.format}
		 * @param {function} fn a function producing the value to format
		 * @param {object} size a size object with elements {top, bottom, left, right}
		 */
		obj.draw = function draw(pos, type, fn, size) {
		    if (obj.cfg().visible) {
		        var rot = parseFloat(obj.cfg('rotate'));
    			if (obj.strings && obj.strings.length > 0) { 
                    var area = obj.cfg().elem; 
        			var l = area.selectAll('.'+obj.cfg().cssClass).data(obj.strings);
        			size = size || {top:1e99, bottom:-1e99, left:1e99, right:-1e99};
       
        			if (obj.links) { addLinkLabel(l.enter(), pos, obj.cfg(), obj.links[0]); }
        			else { addLabel(l.enter(), pos, obj.cfg()); }
        			setText(size, l, function(d, i) { return util.format(type, (fn)? fn(d, i) : d); });
        			l.transition().duration(obj.cfg().transition).attr('x', pos[0]).attr('y', pos[1]);
        			l.exit().remove();
        			if (rot !== 0) { rotate(size, rot*Math.PI/180); }
        			return size;
        		}
    		}
    		return size || {top:0, bottom:0, left:0, right:0};
		};

		return obj;
	};
}]);

