angular.module('hsGraph').factory('HsLineSeries', ['HsSeries', function(HsSeries) {
    "use strict";    
    return function(elem) {
        var obj = new HsSeries(elem, 'line', 'Line Series');
        obj.cfg().area.visible = false;
        obj.markers.cfg().visible = true;
        obj.cfg().lines.visible = true;
        return obj;
    };
}]);



