angular.module('hsGraph').factory('hsGraphUtil', ['hsDataType', function(dataType) {
    "use strict";    
    
    function format(type, t) {
        switch(type) {
            case 'date':    t = d3.time.format('%-m/%-d/%y')(dataType.toDate(t)); break;
            case 'log' :
            case 'linear':  if (typeof t === 'number') { t = d3.format('.3s')(t); } break;    
            case 'percent': t = d3.format('%')(t); break;
            case 'index':
            case 'ordinal':
            case 'nominal': 
        }
        return t;
    }

/**
 * @ngdoc method
 * @name .#getTooltip
 * @methodOf hsGraph.hsGraphUtil
 * @param {hsGraph.object.HsSeries} obj - the series object holding information on the data fields used.
 * @param {Array} d - the data sample array from which to construct the tooltip.
 * @param {Boolean} html - if true, uses '<br>' for line breaks; else uses '\r'
 * @returns {String} the tooltip string describing the data sample.
 * @description returns a tooltip string for a data sample and a series context.
 */
    function getTooltip(obj, d, html) {
        var result = '';
        html=false;
        if (!obj.keys.tip) {
            var x, y, l, x1, y1, x2, y2;
            var name='', label='', xval='', yval='';
            var br = html? '<br>' : ' '; // '\r';
            var _b = html? '<b>' : '';
            var _b_= html? '</b>' : '';
            var index = obj.data.indices;
            if (d) {
                x = d[index.xOrg]; y = d[index.yOrg]; 
                if (index.labelOrg !== index.yOrg) { l = d[index.labelOrg]; }
                if (index.x !== index.xOrg) { x1 = d[index.x]; }
                if (index.y !== index.yOrg) { y1 = d[index.y]; }
                if (index.x2) { x2 = d[index.x2]; }
                if (index.y2) { y2 = d[index.y2]; }
            }
            if (obj.cfg().compName !== undefined) { name = _b + obj.cfg().compName + _b_; }
            if (l) { label = l; }
            if (x) { xval = obj.keys.x[0] + ': ' + _b + format(obj.xaxis.cfg().scaleType, x) + _b_; }
            if (x1) { xval += ' (' + format(obj.xaxis.cfg().scaleType, x1) + ')'; }
            if (x2) { xval += br + obj.keys.x[1] + ': ' + format(obj.xaxis.cfg().scaleType, x2); }
            if (y) { yval = obj.keys.y[0] + ': ' + _b + format(obj.yaxis.cfg().scaleType, y) + _b_; }
            if (y1) { yval += ' (' + format(obj.yaxis.cfg().scaleType, y1) + ')'; }
            if (y2) { yval += br + obj.keys.y[1] + ': ' + format(obj.yaxis.cfg().scaleType, y2); }
            result = name + br + label + br + xval + br + yval;
            result = result.trim().replace(/\r{2,}/g, '\r');        // replace double \r with single \r
            result = result.trim().replace(/(<br>){2,}/g, '<br>');  // replace double <br> with single <br>
        } else {
            result = obj.keys.tip.slice();  		// create copy of the tip string
            var cols = result.match(/<([^>]+)>/g);	// find all <vars>
            if (cols) {            	
	            for (var i=0; i<cols.length; i++) {              
	                var col=cols[i].slice(1, -1);		// remove brackets <, >
	                var pct = false;
	                if (col.charAt(col.length-1)==='%') {
	                    pct = true;
	                    col=col.slice(0,-1);			// remove % sign
	                }
	                if (col === '_x') {
	                	if (obj.data.indices.x === obj.data.colNumber('x')) { col = 'x'; }
	                	else { col = obj.keys.x; }
	                } else if (col === '_y') {
	                	if (obj.data.indices.y === obj.data.colNumber('y')) { col = 'y'; }
	                	else { col = obj.keys.y; }
	                } else if (col === '_l') {
	                	col = obj.keys.labels;
	                } else if (col === '_ycum') {
	                	if (obj.data.indices.y === obj.data.colNumber('y')) { col = 'y_Cum'; }
	                	else { col = obj.keys.y + '_Cum'; }
	                }
	                col = obj.data.colNumber(col);		// col contains current column var
	                if (d && col>=0) { 
	                    var val = d[col];
	                    if (val instanceof Date) { val = format('date', val); }
	                    else if (pct) { val = format('percent', val/100); }
	                    else { val = format('linear', val); }
	                    result = result.replace(cols[i], val); // replace <var> with column content
	                }                
	            }
            }
        }
        return result;
    }


/**
 * @ngdoc method
 * @name .#correct
 * @methodOf hsGraph.hsGraphUtil
 * @param {number | number []} val - the number or array to correct.
 * @returns {number | Array} the corrected number, or an array with corrected entries.
 * @description limits values to the range [0, 100] to be used as percentage value in attributes.
 * If `val` is `NaN` then it is repleced with `0`.
 * If `val` is an array, this method applies itself to all of `val`'s elements.
 */
    function correct(val) {
        if (val instanceof Array) {
            for (var i in val) { val[i] = correct(val[i]); }
        } else {
            if (isNaN(val)) { val = 0; }
            val = Math.min(100, Math.max(0, val));
        }
        return val;
    }

/**
 * @ngdoc method
 * @name .#gradient
 * @methodOf hsGraph.hsGraphUtil
 * @returns {function} a function to call for applying the gradient. 
 * The function takes the DOM element and a gradient id as parameters.
 * @description 
 * Define a gradient function that can then be applied to specific DOM elements.
 * @param {Object}   cfg gradient configuration objects
 * @param {number}   cfg.angle gradient angle in degrees, 0 degrees points left to right
 * @param {Object[]} cfg.stops array of stops
 * @param {percent}  cfg.stops[].offset stop offset in %
 * @param {number}   cfg.stops[].k positive numbers darken and negative numbers lighten the default color
 * @param {number}   cfg.stops[].opacity ranges from 0 to 1
 * @example
 <example module="hsGraph">
  <file name="index.html">
    <div style='width: 100%; height: 100px;' class='myGradient'>	
    	<svg width='100%' height='100%' class='grd2'> 
    		<defs></defs>
    		<rect width='100%' height='100%'></rect>
    	</svg>
    </div>
  </file>
  <file name="script.js">
angular.module('hsGraph').directive('myGradient', ['hsGraphUtil', function(hsGraphUtil){
	return {
		restrict: 'C',
		link: function(scope, ele, attrs) {
			var grd2 = hsGraphUtil.gradient({angle:-0, stops:[{offset:'0%', k:-0.1}, {offset:'80%', k:-0.9}, {offset:'100%', k:-0.9}]});
			grd2(hsGraphUtil.d3.select($('.grd2 rect')[0]), "id2");
		}
	}; 
}]);
  </file>
  <file name="style.css">
.grd2 rect	{ fill: #6a6; }
  </file>
</example>
*/
    var gradient = function gradient(cfg) {
        var rad = Math.PI * cfg.angle / 180;  
        
        return function(el, id) {
            id = id.replace(/ /g, '-');     // substitute all spaces with '-'
            var watchdog = 5;
            var defs;    // d3.select("#invisibleG").select('defs')
            // find the <defs> section
            var parent = el;
            while (true) {
                defs = parent.select('defs');
                if (defs[0][0] !== null) { break; }
                if (--watchdog < 0) { console.log('did not find defs'); return; }
                parent = parent.select(function() { return this.parentNode; }); 
            }
            if (defs[0].length>1) { defs[0].splice(0, defs[0].length-1); } // add only a single gradients def
            var eid = id + Math.round(1e6*Math.random());   // add unique number to allow for multiple graphs per html
            var grd = defs.select("."+id);  // the class is the id without unique number
            if (grd[0][0] === null) { // create new unique gradient
                var color = el.style('fill');
                var c0 = d3.rgb(color);
                grd = defs.append('linearGradient').attr('id',eid).attr('class',id)
                      .attr('x1', '0').attr('x2', Math.cos(rad)).attr('y1', '0').attr('y2', -Math.sin(rad));
                for (var i=0; i<cfg.stops.length; i++) {
                    var stop = grd.append('stop');
                    var k = cfg.stops[i].k || 0.1;
                    var c = c0.darker(k);
                    stop.attr('offset', cfg.stops[i].offset).attr('stop-color', ''+c);
                    if (cfg.stops[i].opacity) { stop.attr('stop-opacity', cfg.stops[i].opacity); }
                }
            } else {    // gradient of class id already exists
                eid = grd.attr('id');
            }
            el.attr('style', 'fill:url(#' + eid + ')'); 
        };
    };
    

    // standard header gradient
    gradient.header = gradient({angle:-90, stops:[{offset:'0%', k:-0.05}, {offset:'80%', k:-0.2}, {offset:'100%', k:-0.2}]});

    return {
        correct:    correct,
        format:     format,
        getTooltip: getTooltip,
        d3:         d3,
        gradient:   gradient,
        animationDuration: 100
    };
}]);
