angular.module('hsGraph').factory('HsHBarSeries', ['HsSeries', 'hsGraphUtil', '$compile', function(HsSeries, util, $compile) {
    "use strict";    
    function drawMarkers(obj) {
        return function(coord) {
            var index = obj.data.indices;
            var x = index.x, x2 = index.x2, y = index.y;
            var el;
            var heightFct = (100-(parseInt(obj.markers.cfg().gap||0)))/100.0;

            function xpos(d)    { return coord.x.get(d[x]) + '%'; }
            function ypos(d)    { return coord.y.getRaw(d[y]) + '%'; }
            function width(d)   { 
                return (coord.x.get(d[x2])-coord.x.get(d[x])) + '%'; }
            function height()   { return util.correct(obj.yaxis.getRangeBand()*heightFct)+'%'; }
            function tooltip(d) { return util.getTooltip(obj, d, true); }
            function colorClass(d)   { return d[index.color]? d[index.color].replace(' ','_') : ''; }

            obj.markers.draw(obj.data(), {xpos:xpos, ypos:ypos, width:width, height:height, colorClass:colorClass}, tooltip);
            $compile($(obj.markerGroup[0]))(this.myScope);  
            
            if (obj.keys.colors !== "") {
                var domain = obj.data.colDomain(obj.keys.colors);
                for (var i=0; i<domain.length; i++) {
                    var c = domain[i].replace(' ','_');
                    el = obj.markerGroup.selectAll('.hs-series-marker.'+c);
                    if (el[0].length > 0) {
                        obj.markerGroup.gradient(el, (obj.cfg().compName+c || '') + 'HBarGrd');
                    }
                }
            } else {
                el = obj.markerGroup.selectAll('.hs-series-marker');
                if (el[0].length > 0) {
                    obj.markerGroup.gradient(el, (obj.cfg().compName || '') + 'HBarGrd');
                }
            }
        };
    }

    return function(elem) {
        var obj = new HsSeries(elem, 'hbar', 'Horizontal Bar Series');
        obj.cfg().lines.visible = false;
        obj.cfg().area.visible = false;

        obj.drawMarkers = drawMarkers(obj);    
        return obj;
    };
}]);

