angular.module('hsGraph').factory('HsGraph', 
['HsComponent', 'HsLabels', 'hsGraphUtil', function HsGraphFactory(HsComponent, HsLabels, util) {
    "use strict";
        
    function panelSize(elem) {
        var size = [parseInt(elem.css('width')), parseInt(elem.css('height'))];
        return size;
    }
    
    function getMargins(cc, hasTitle) {
        var i, pct;
        var graph = cc.graph[0];
        var chartMargin = {top:(hasTitle?0:2), left:2, bottom:2, right:2};
        for (i=0; i<cc.xaxis.length; i++) {
            pct = 100*cc.xaxis[i].axisSize()/panelSize(graph.ngElem)[1];
            if (cc.xaxis[i].cfg().primary) { chartMargin.bottom = Math.max(chartMargin.bottom, pct); }
                                      else { chartMargin.top    = Math.max(chartMargin.top, pct); }
        }
        for (i=0; i<cc.yaxis.length; i++) {
            pct = 100*cc.yaxis[i].axisSize()/panelSize(graph.ngElem)[0];
            if (cc.yaxis[i].cfg().primary) { chartMargin.left  = Math.max(chartMargin.left, pct); }
                                    else { chartMargin.right = Math.max(chartMargin.right, pct); }
        }
        cc.chartMargin = chartMargin;
        return chartMargin;
    }
    
    function setStack(cc) {
        if (cc.series.stacked.length>0) {
            var stack = util.d3.layout.stack()
                .offset(cc.series.stacked.offset)  
                .values(function(d) { return d; }) 
                .x(function(d, i) { return d.org.values[i][d.org.indices.x]; })
                .y(function(d, i) { return d.org.values[i][d.org.indices.y]; })
                .order('reverse');
            stack(cc.series.stacked);
        }
    }
        
    function initComponents(cc) {
        var i;
        if (cc.chart.length>0) { cc.chart[0].init(); }
        cc.series.stacked = [];

        for (i=0; i<cc.xaxis.length; i++) { cc.xaxis[i].init(); }
        for (i=0; i<cc.yaxis.length; i++) { cc.yaxis[i].init(); }
        for (i=0; i<cc.hgrid.length; i++) { cc.hgrid[i].init(); }
        for (i=0; i<cc.vgrid.length; i++) { cc.vgrid[i].init(); }
        for (i=0; i<cc.series.length; i++) { cc.series[i].init(cc.series); }
        for (i=0; i<cc.legend.length; i++)  { cc.legend[i].init(); }

        setStack(cc);
    }

    function setComponentSizes(cc, hasTitle) {
        var i;
        var chartMargin = getMargins(cc, hasTitle);
        if (cc.chart.length>0) { cc.chart[0].setSize(chartMargin); }

        for (i=0; i<cc.xaxis.length; i++)   { cc.xaxis[i].setSize(chartMargin); }
        for (i=0; i<cc.yaxis.length; i++)   { cc.yaxis[i].setSize(chartMargin); }
        for (i=0; i<cc.hgrid.length; i++)   { cc.hgrid[i].setSize(chartMargin); }
        for (i=0; i<cc.vgrid.length; i++)   { cc.vgrid[i].setSize(chartMargin); }
        for (i=0; i<cc.series.length; i++)  { cc.series[i].setSize(chartMargin, cc.xaxis[0], cc.yaxis[0]); }
        for (i=0; i<cc.legend.length; i++)  { cc.legend[i].setSize(chartMargin, cc.series); }
    }

    function setDomains(cc) {
        function extendDom(dom, newDom, type) {
            if (type === 'ordinal') {
                for (var c=0; c<newDom.length; c++) {
                    var key = newDom[c];
                    if (key === +key) { key = key+'_'; }
                    if (!dom[key]) {
                        dom.push(newDom[c]+'');
                        dom[key] = true;
                    }
                }
            } else if (newDom && newDom.length === 2) {
                dom[0] = dom[0]? Math.min(dom[0], newDom[0]) : newDom[0]; 
                dom[1] = dom[1]? Math.max(dom[1], newDom[1]) : newDom[1]; 
            }
            if (type === 'date') {
                dom[0] = new Date(dom[0]);
                dom[1] = new Date(dom[1]);
            }
        }
        
        var i;
        var xdom=[], ydom=[];
        var typex = cc.xaxis[0].cfg('scaleType');
        var typey = cc.yaxis[0].cfg('scaleType');
        for (i=0; i<cc.series.length; i++)  {
            var s = cc.series[i];
            var dsx = s.cfg('noScaleX') || false;
            var dsy = s.cfg('noScaleY') || false;
            var x = s.data.indices.x, x2 = s.data.indices.x2;
            var y = s.data.indices.y, y2 = s.data.indices.y2;
            var sxdom = s.data.colDomain(x), sydom = [];
            if (x2) { extendDom(sxdom, s.data.colDomain(x2), typex); }
            if (s.cfg('shared')) { 
                sydom = [0,1]; 
            } else if (s.cfg('stacked')) { 
                sydom = [0, s.data.aux[s.data.aux.length-1].y]; 
            } else {
                sydom = s.data.colDomain(y);
                if (y2) { extendDom(sydom, s.data.colDomain(y2), typey); }
            }
            
            if (!dsx) { extendDom(xdom, sxdom, typex); }
            if (!dsy) { extendDom(ydom, sydom, typey); }
        }  
        if (cc.series.stacked.length>0) {
            // stack was built in 'reverse', therefore index 0 holds max
            ydom[1] = util.d3.max(cc.series[0].data.aux, function(row) { return row.y+row.y0; });
        }
        cc.xaxis[0].setDomain(xdom);
        cc.yaxis[0].setDomain(ydom);
    }

    function drawComponents(cc) {
        var i;
        if (cc.chart.length>0) { cc.chart[0].draw(); }
        for (i=0; i<cc.legend.length; i++)  { cc.legend[i].draw(); }
        for (i=0; i<cc.xaxis.length; i++)   { cc.xaxis[i].draw(); }
        for (i=0; i<cc.yaxis.length; i++)   { cc.yaxis[i].draw(); }
        for (i=0; i<cc.hgrid.length; i++)   { cc.hgrid[i].draw(cc.yaxis['primary']); }
        for (i=0; i<cc.vgrid.length; i++)   { cc.vgrid[i].draw(cc.xaxis['primary']); }
        for (i=0; i<cc.series.length; i++)  { cc.series[i].draw(); }
    }
    
    function drawChartHeader(obj, chartTitle) {
        chartTitle.strings = [obj.cfg().chartTitle];
        if (obj.cfg().chartTitleLink) { chartTitle.links = [obj.cfg().chartTitleLink]; }
        chartTitle.draw(["50%","50%"]);
    }
    
    var grd = util.gradient.header;
    
	function HsGraphInit(ngElem) {
	    var elem = util.d3.select(ngElem[0]);
		var obj = new HsComponent('graph', elem, 'Graph').cfg({header:true});
		var chartTitle = new HsLabels(elem.select('.hs-graph-header'), 'Chart Title', obj).cfg({ 
			x: '50%', y: '50%',
			anchor: 'middle',
			baseline: 'middle',
			cssClass: 'hs-graph-header-label'
		});
		
		var e = elem.select('.hs-graph-header rect');
        var fill = e.style('fill');
        if (fill !== 'none') { 
            var id = (obj.cfg().compName || '') + 'GraphHeaderGrd';
            grd(e, id); 
        }

        obj.ngElem = $(elem[0]);
		obj.init = initComponents;

		obj.setTextSize = function setTextSize(cb) {
            var ps = panelSize(ngElem);
            var px = Math.min(ps[0], ps[1]) / 24; 
            $(ngElem).closest('hs-layout').animate({'font-size': px+'px'}, util.animationDuration, 'linear', cb);
            cb();       
        };

		obj.draw = function draw(cc) {
            setDomains(cc);
            var hasTitle = true;
		    if (!obj.cfg('chartTitle')) {
		        hasTitle = false;
                obj.ngElem.find('.hs-graph-header').attr('height', '0%');
                obj.ngElem.find('.hs-graph-body').attr('height', '100%').attr('y','0%');
		    }
            setComponentSizes(cc, hasTitle);
            if (obj.cfg('header')) { drawChartHeader(obj, chartTitle); }
            drawComponents(cc);
		};
		return obj;
	}
	
	var result = HsGraphInit;
	return result;
}]);
