angular.module('hsGraph').factory('HsAreaSeries', ['HsSeries', function(HsSeries) {
    "use strict";    
    return function(elem) {
        var obj = new HsSeries(elem, 'area', 'Area Series');
        obj.cfg().area.visible = true;
        obj.markers.cfg().visible = true;
        obj.cfg().lines.visible = true;
        return obj;
    };
}]);
