angular.module('hsGraph').factory('HsYAxis', ['HsAxis', 'hsGraphUtil', function(HsAxis, util) {
    return function(elem, primary) {
        var obj = new HsAxis(false, elem, primary, 'Y Axis');

        var w = obj.cfg().tickWidth,
            m = obj.cfg().tickMargin;
        var axisLabelCfg = {
                pos     : obj.cfg().primary? '0%' : '10%',
                dx      : '-'+(w+m)+'px',
                dy      : '0.3em',
                anchor  : obj.cfg().primary? 'end':'start'
                };
        obj.axisMajorLabels.cfg(axisLabelCfg);
        obj.axisMinorLabels.cfg(axisLabelCfg);

        obj.setSize = function(margins) {
            obj.requestRange([100-margins.bottom, margins.top]);
            if (obj.cfg().elem) { obj.cfg().elem.transition().duration(obj.cfg().transition)
                .attr("x", util.correct(primary?0:(100-margins.right))+'%').attr("y", '0%')
                .attr('height', '100%').attr('width', util.correct(primary?margins.left:margins.right)+'%');
            }
            obj.getTickMarks();
        };
        return obj;
    };
}]);

