angular.module('hsGraph').factory('HsScale', ['hsGraphUtil', 'HsConfigurable', function HsScaleFActory(util, HsConfigurable) {
	return function(scaleType, name, parent) {
		var scale;
		var ticks = [];
		var obj = new HsConfigurable("scale", name, parent).cfg({
			type: 		     'scale',
			base:		     10,
			padding:         0.1,
			outerPadding:    0.1,
			scaleType:       scaleType
		});
		var axisTypes = {
			'index': 	util.d3.scale.ordinal,
			'nominal': 	util.d3.scale.ordinal,
			'ordinal': 	util.d3.scale.ordinal,
			'log': 		util.d3.scale.log,
			'date': 	util.d3.time.scale,
            'percent':  util.d3.scale.linear,
            'linear':   util.d3.scale.linear
		};
		scale = axisTypes[scaleType]() || axisTypes['index']();
		if (!scale.rangeExtent) { scale.rangeExtent = scale.range; }
		for (var a in obj) { scale[a] = obj[a]; }

		/**
		 * Sets the base to use in case of a logarithmic scale.
		 * @memberof HsScale
		 * @function setBase
		 * @instance
		 * @default 10
		 * @param {Object} [d] Object literal containign key value pairs to  add or replace in the cfg obkect.
		 * @returns nothing.
		 */
		scale.setBase = function(b) {     // used for logarithmic scales
		    if (obj.cfg().base!==10 && obj.cfg().base!==b) {
		        console.log('warning: base set twice!');
		    }
		    obj.cfg().base = b; 
		 };	

		/**
		 * Gets an array of major ticks domain values for the scale under its current settings
		 * @memberof HsScale
		 * @function getMajorTicks
		 * @instance
		 * @returns {float[]|Date[]} An array of tick domain values
		 */
		scale.getMajorTicks = function() {
			switch(scaleType) {
				case 'log':
					var base = scale.base();
					var result = [];
					for (var i in ticks) {
						var e = Math.log(ticks[i]) / Math.log(base);
						if (Math.abs(e - Math.round(e)) < 0.01) { result.push(ticks[i]); }
						}
					return result;
				case 'ordinal': 
				case 'nominal': 
					var _t = scale.domain().slice(0);
					return _t;
				default: return scale.ticks(3);
			}
		};

		/**
		 * Gets an array of minor ticks domain values for the scale under its current settings
		 * @memberof HsScale
		 * @function getMinorTicks
		 * @instance
		 * @returns {float[]|Date[]} An array of tick domain values
		 */
		scale.getMinorTicks = function() {
			switch(scaleType) {
				case 'nominal': 
				case 'ordinal': return scale.domain().slice(0);
				default: return scale.ticks(10);
			}
		};
     

		/**
		 * Sets the scale range in percent of the graph panel size.
		 * @memberof HsScale
		 * @function setRange
		 * @instance
		 * @param {float[]} range [min, max], in %, of the screen range to scale to.
		 * @returns nothing.
		 */
		scale.setRange = function setRange(range) {
			switch (scaleType) {
				case 'index':
				case 'nominal': 
				case 'ordinal': scale.rangeBands(range, scale.cfg().padding, scale.cfg().outerPadding); 
								break;
				default: 		scale.range(range);
			}
			return scale;
		};
 
		/**
		 * Sets the domain upon which to scale.
		 * @memberof HsScale
		 * @function setDomain
		 * @instance
		 * @param {float[]|Date[]} domain [min, max] values of the domain upon which to scale. Values are either floats or Dates, depending on cfg.scaleType.
		 * @returns nothing.
		 */
		scale.setDomain = function(domain) {
            function autoDomain(values) {
                var result = [1e99, -1e99];
                for (var i=0; i<values.length; i++) {
                    result[0] = Math.min(result[0], values[i]);
                    result[1] = Math.max(result[1], values[i]);
                }
                return result;
            }

			function autoLogDomain(values) {
				var base = obj.cfg().base;
				var result= util.d3.extent(values);
				scale.base(base);
				if (result[0] <= 0) { result[0] = result[1] / (base*base*base); }
				return result;
			}

			function ordinalDomain(values) { return values; }

			switch (scaleType) {
				case 'date': 	scale.domain(autoDomain(domain)).nice(); break;
				case 'index':
				case 'nominal': 
				case 'ordinal': scale.domain(ordinalDomain(domain));break;
				case 'percent':
				case 'linear': 	scale.domain(autoDomain(domain)).nice();break;
				case 'log': 	scale.domain(autoLogDomain(domain)); break;
				default: 		console.log('unknown domain: ' + scaleType);
			}
			if (scaleType === 'ordinal' || scaleType === 'nominal') { 
			    ticks = domain.slice(0);
			} else { 
			    ticks = scale.ticks(4); 
			}
		};
		return scale;
	};
}]);
