angular.module('hsGraph').factory('HsAxis', ['HsComponent', 'HsLabels', 'HsScale', 'hsGraphUtil', function HsAxisFactory(HsComponent, HsLabels, HsScale, util) {
	function setup(obj) {
	    var cfg = obj.cfg();
		var x = cfg.type === 'xaxis';
		var prim = cfg.primary;
		var w = cfg.tickWidth,
			m = cfg.tickMargin;
		var elem = cfg.elem;
		if (elem) {
    		elem.attr('overflow', 'visible')
    			.attr('class', 	'hs-'+ (x?'x':'y') +'-axis hs-axis-' + (prim?'primary':'secondary'));
    		elem.append('rect')
    			.attr('class', 	'hs-axis-area')
    			.attr('x', '0%').attr('y', '0%')
    			.attr('height', '100%').attr('width', '100%');
    		elem.append('line')
    			.attr('class', 	'hs-axis-line')
    			.attr('x1', '0%').attr('y1', '0%').attr('x2', '0%').attr('y2', '0%');
    
    		// setup svg for tick marks
    		obj.tickElem = elem.append('svg').attr('class', 'hs-axis-tick-mark-group');
    		if (x) { obj.tickElem
    			.attr('y', prim?'0%':'100%').attr('height', w+'px')
    			.attr('x', '0').attr('width', '100%');
    		} else { obj.tickElem
    			.attr('x', prim?'100%':'0%').attr('width', w+'px')
    			.attr('y', '0').attr('height', '100%');
    		}
    		// setup svg for labels
    		obj.labelElem = elem.append('svg').attr('class', 'hs-axis-label-group');
    		if (x) { obj.labelElem.attr('y', (w+m-3)+'px'); }
    		else   { obj.labelElem.attr('x', '100%'); }
        }

        /**
         * @ngdoc object
         * @name axisTitle
         * @propertyOf hsGraph.object.HsAxis
         * @description An {@link hsGraph.object.HsLabels HsLabels} property to display the axis title.
         * To configure the labels, use `axis.axisTitle.cfg({})` with parameters documented in 
         * {@link hsGraph.object.HsLabels#description_cfg_options HsLabels cfg options} 
         */
        obj.axisTitle  = new HsLabels(obj.cfg().elem, "Axis Title", obj).
            cfg({ cssClass: 'hs-axis-title' });
    
        /**
         * @ngdoc object
         * @name axisMajorLabels
         * @propertyOf hsGraph.object.HsAxis
         * @description An {@link hsGraph.object.HsLabels HsLabels} property to display the major axis labels
         * To configure the labels, use `axis.axisMajorLabels.cfg({})` with parameters documented in 
         * {@link hsGraph.object.HsLabels#description_cfg_options HsLabels cfg options} 
         */
        obj.axisMajorLabels = new HsLabels(obj.labelElem, "Major Label", obj).
            cfg({cssClass: 'hs-axis-label-major'});
        /**
         * @ngdoc object
         * @name axisMinorLabels
         * @propertyOf hsGraph.object.HsAxis
         * @description An {@link hsGraph.object.HsLabels HsLabels} property to display the minor axis labels
         * To configure the labels, use `axis.axisMinorLabels.cfg({})` with parameters documented in 
         * {@link hsGraph.object.HsLabels#description_cfg_options HsLabels cfg options} 
         */
        obj.axisMinorLabels = new HsLabels(obj.labelElem, "Minor Label", obj).
            cfg({cssClass: 'hs-axis-label-minor', visible: false});
    }

	function drawLine(x, axis) {
		var yx = axis.cfg().primary?100:0,
			xy = axis.cfg().primary?0:100;
		var r = util.correct(axis.getRangeExtent());
		if (axis.cfg().elem) { axis.cfg().elem.selectAll('.hs-axis-line').transition().duration(axis.cfg().transition)
			.attr('x1', (x?r[0]:yx) + '%')
			.attr('y1', (x?xy:r[0]) + '%')
			.attr('x2', (x?r[r.length-1]:yx) + '%')
			.attr('y2', (x?xy:r[r.length-1]) + '%');
		}
	}

	function drawTicks(x, axis, cssClass, ticks) {
        var pad  = axis.getGridPadding();
		var t  = axis.tickElem.selectAll('.'+cssClass).data(ticks);	// bind new data

		t.enter().append('line').attr('class', cssClass)
	    	.attr(x?'y1':'x1', '0%').attr(x?'y2':'x2', x?'100%':'-100%');

		t.transition().duration(axis.cfg().transition)
	    	.attr((x?'x':'y')+'1', function(d) { return util.correct(axis.scale(d)-pad) + '%'; })
	    	.attr((x?'x':'y')+'2', function(d) { return util.correct(axis.scale(d)-pad) + '%'; });

		t.exit().remove();
	}

	function drawLabels(x, obj) {
		var offset = obj.getLabelOffset();
		obj.axisMajorLabels.strings = obj.majorTicks;
		obj.axisMinorLabels.strings = obj.minorTicks;
		function xpos(d) { return util.correct(obj.scale(d) + offset) + '%';}
		function ypos() { return obj.axisMajorLabels.cfg().pos;}
		var r = obj.axisMajorLabels.draw(x?[xpos,ypos]:[ypos,xpos], obj.cfg().scaleType);
		if (obj.cfg().scaleType !== 'log') {
			r = obj.axisMinorLabels.draw(x?[xpos,ypos]:[ypos,xpos], obj.cfg().scaleType, null, r);
		}
		return r;
	}

	function drawAxisTitle(obj) {
		obj.axisTitle.strings = [obj.cfg().title];
		return obj.axisTitle.draw([obj.axisTitle.cfg().x, obj.axisTitle.cfg().y]);
	}

    
	return function(x, elem, primary, name) {
	    var size, type, scale, obj;    
	    
	    function getTickMarks() { 
            obj.majorTicks = scale.getMajorTicks();
            obj.minorTicks = scale.getMinorTicks();
            for (var i=0; i<obj.majorTicks.length; i++) {
                var j;
                switch (obj.cfg().scaleType) {
                    case 'date':   for (j=0; j<obj.minorTicks.length; j++) {
                                       if (obj.minorTicks[j].getTime() === obj.majorTicks[i].getTime()) {
                                           obj.minorTicks.splice(j, 1);
                                       }
                                    }
                                    break;
                    default:        j = obj.minorTicks.indexOf(obj.majorTicks[i]);
                                    if (j>=0) { obj.minorTicks.splice(j, 1); }
                }
            }
        }
        
        function adjustDomain(dom, defDom, type) {
            if (defDom.length === 2) {
                if (defDom[0] !== 'auto') { dom[0] = defDom[0]; }
                if (defDom[1] !== 'auto') { dom[1] = defDom[1]; }
            }
            switch(type) {
                case 'date': break;
                case 'linear':
                case 'percent':
                case 'log': dom[0] = +dom[0]; dom[1] = +dom[1];
            }
        }
        
		type = x? 'xaxis' : 'yaxis';
		obj = new HsComponent(type, elem, name).cfg({
			compName: primary? 'primary' : 'secondary',
//			scaleType: 'linear',
			tickWidth:   3,			// in pixel
			tickMargin:  2,			// gap, in pixels, between tick and label
			labelMargin: 10,		// gap, in pixels, between label and edge
			expand: 0,              // factor by which to expand the domain below min and above max  
			defDomain: []
		});
		size = obj.cfg().tickWidth + obj.cfg().tickMargin + obj.cfg().labelMargin;

		obj.cfg().primary	   = primary;

        obj.getTickMarks = getTickMarks;
		obj.getSize = function getSize() { return size; };	// height/width of x/y axis in pixel
		
		obj.scale = function(d) { return scale(d); };
		
        /**
         * @memberof HsAxis
         * @function requestRange
         * @instance
         * @param {float[]} range [min, max], in %, of the screen range to scale to.
         * @returns nothing.
         * @description Requests a scale range in percent of the graph panel size. 
         * The final range will chosen to span all requests.
         */
        obj.requestRange = function requestRange(range) {
            obj.requestRange.range =  range;
            scale.setRange(range);
        };
        
        obj.getRangeExtent = function() { return scale.rangeExtent(); };

		obj.axisSize = function axisSize() {	
/*		     	    
			var domain = (obj.cfg().scaleType === 'ordinal')?
                getOrdinalDomain(series) : getContinuousDomain(series);
            obj.setDomain(domain);
*/            
		    obj.draw();
            return obj.getSize();
		};
			
        obj.setDomain = function setDomain(dom) {
            var expand = obj.cfg().expand;
            var span = (dom[1] - dom[0]) * expand;
            switch (obj.cfg().scaleType) {
                case 'date':    if (dom[1] instanceof Date) { dom[1] = dom[1].getTime(); }
                                dom[1] = new Date(dom[1]+span);                                
                                break;
                case 'linear':  dom[0] -= span;
                                dom[1] += span;                                
                                break;
                case 'percent': dom[0] -= span;
                                dom[1] += span;     
                                break;
                case 'log':     span = 1.5;  
                                dom[0] /= span;
                                dom[1] *= span;                                
                                break;
            }
            adjustDomain(dom, obj.cfg().defDomain, obj.cfg().scaleType);
            scale.setDomain(dom); 
            obj.getTickMarks();
        };
        
        obj.getGridPadding = function getGridPadding() {
            if (obj.cfg().scaleType === 'ordinal' || obj.cfg().scaleType === 'nominal') { 
                return scale.rangeBand() * scale.cfg().outerPadding/2; 
            } else { 
                return 0; 
            }
        };
        
        obj.getLabelOffset = function getLabelOffset() {
            if (obj.cfg().scaleType === 'ordinal' || obj.cfg().scaleType === 'nominal') { 
                return scale.rangeBand()/2 - scale.cfg().outerPadding; 
            } else { 
                return 0; 
            }
        };
        
        obj.getRangeBand = function getRangeBand() {
            if (scale.rangeBand) { return scale.rangeBand(); }
                            else { return 0; }
        };
        
        obj.init = function init() {
            if (!scale) { scale = new HsScale(obj.cfg().scaleType, "Axis Scale", obj); }
        };

		obj.draw = function() {
			if (!obj.cfg().visible) { return; }

			drawLine(x, obj);
			drawTicks(x, obj, 'hs-axis-tick-major', obj.majorTicks);
			drawTicks(x, obj, 'hs-axis-tick-minor', obj.minorTicks);
			var r = drawLabels(x, obj);
			var q = drawAxisTitle(obj);
			size = 0;

            if (r) { size = Math.max(size, (x? (r.bottom-r.top) : (r.right-r.left))); } // size in pixel
            if (q) { size = Math.max(size, (x? (q.bottom-q.top) : (q.right-q.left))); } // size in pixel
			size += obj.cfg().tickWidth + obj.cfg().tickMargin + obj.cfg().labelMargin;
		};

		setup(obj);
		return obj;
	};
}]);

