angular.module('hsGraph').factory('HsChart', ['HsComponent', 'hsGraphUtil', function HsChartFactory(HsComponent, util) {
	return function(elem) {
		var obj = new HsComponent('chart', elem, 'Chart').cfg({
            gradient: { angle: -90, stops: [
                {offset:'0%', k:-0.2}, 
                {offset:'80%', k:-0.2}, 
                {offset:'100%', k:-0.05}
            ]}

		});

        obj.init = function() {
        };

		obj.setSize = function(margins) {
			obj.cfg().elem.transition().duration(obj.cfg().transition)
				.attr("x", util.correct(margins.left)+'%').attr("y", util.correct(margins.top)+'%')
				.attr('height', util.correct(100-margins.top-margins.bottom)+'%').attr('width', util.correct(100-margins.left-margins.right)+'%');
		};
		
		obj.draw = function() {
		    if (obj.cfg().gradient) {
                if (!obj.gradient) {
                    obj.gradient = util.gradient(obj.cfg().gradient);
                }
                obj.gradient(obj.cfg().elem, (obj.cfg().compName || '') + 'ChartGrd');
            }
   		};
		
		return obj;
	};
}]);

