angular.module('hsGraph').directive('hsGrid', ['hsGraphUtil', 'HsVGrid', 'HsHGrid', function(util, HsVGrid, HsHGrid) {
	return {
		restrict: 'EA',
		replace: true,
		require: '^hsGraph',
	    template: '<svg class="hs-grid"></svg>',
		link: function(scope, ele, attrs, ctrl) {
			var major = (attrs.minor!==undefined)? 'minor' : 'major';
			ele.attr('class', 'hs-'+major+'-grid');
			var grid = attrs.dir || 'both';
			if (attrs.horizontal === "") { grid = 'horizontal'; }
			if (attrs.vertical === "")   { grid = (grid === 'horizontal')? 'both' : 'vertical'; }
			switch (grid) {
				case 'vertical': 	ctrl.addComponent(new HsVGrid(util.d3.select(ele[0]), major)); break;
				case 'horizontal': 	ctrl.addComponent(new HsHGrid(util.d3.select(ele[0]), major)); break;
				case 'both': 		ctrl.addComponent(new HsVGrid(util.d3.select(ele[0]), major));
									ctrl.addComponent(new HsHGrid(util.d3.select(ele[0]), major));
									break;
				default:			ctrl.addComponent(new HsVGrid(util.d3.select(ele[0]), major));
			}

		}
	};
}]);

