angular.module('hsGraph').factory('HsGrid', ['HsComponent', 'hsGraphUtil', function HsGridFactory(HsComponent, util) {
	return function(type, elem, major) {
		var h = (type==='hgrid'), 
		    m = (major === 'major');
		var obj = new HsComponent(type, elem, (m?"Major ":"Minor ")+type).cfg({major: major});

		elem.attr('height', '100%').attr('width', '100%');
		if (obj.cfg().elem) {
		   obj.cfg().elem = obj.cfg().elem.append('svg').attr('class', h? 'hs-grid-hor' : 'hs-grid-ver');
        }
        
        obj.init =function() {            
        };
        
		obj.draw = function(axis) {
			if (!obj.cfg().visible) { return; }
			obj.drawGrid(axis, m?axis.majorTicks:axis.minorTicks, m?'hs-grid-major':'hs-grid-minor');
		};

		obj.drawGrid = function(axis, ticks, cssClass) {
			var pad	= axis.getGridPadding();
			var t = obj.cfg().elem.selectAll("."+cssClass).data(ticks);	// bind new data

			// new ticks:
			t.enter().append('line').attr('class', cssClass);

			// update:
			t.transition().duration(obj.cfg().transition)
				.attr(h?'x1':'y1', '0%')
				.attr(h?'x2':'y2', '100%')
		    	.attr(h?'y1':'x1', function(d) { return util.correct(axis.scale(d) - pad) + '%'; })
		    	.attr(h?'y2':'x2', function(d) { return util.correct(axis.scale(d) - pad) + '%'; });

			// old ticks:
			t.exit().remove();
		};
		return obj;
	};
}]);

/**
 * @ngdoc object
 * @name hsGraph.object.HsHGrid
 * @param {d3-selection} elem the d3 selection to render to
 * @param {string} major the strength of the grid, 'major' or 'minor'
 * @description Extends {@link hsGraph.object.HsGrid HsGrid}.
 */
angular.module('hsGraph').factory('HsHGrid', ['HsGrid', 'hsGraphUtil', function(HsGrid, util) {
	return function(elem, major) {
		var obj = new HsGrid('hgrid', elem, major);

		obj.setSize = function(margins) {
			obj.cfg().elem.transition().duration(obj.cfg().transition)
				.attr("x", util.correct(margins.left)+'%').attr("y", '0%')
				.attr('height', '100%').attr('width', util.correct(100-margins.left-margins.right)+'%');
		};
		return obj;
	};
}]);

/**
 * @ngdoc object
 * @name hsGraph.object.HsVGrid
 * @param {d3-selection} elem the d3 selection to render to
 * @param {string} major the strength of the grid, 'major' or 'minor'
 * @description Extends {@link hsGraph.object.HsGrid HsGrid}.
 */
angular.module('hsGraph').factory('HsVGrid', ['HsGrid', 'hsGraphUtil', function(HsGrid, util) {
	return function(elem, major) {
		var obj = new HsGrid('vgrid', elem, major);

		obj.setSize = function(margins) {
			obj.cfg().elem.transition().duration(obj.cfg().transition)
				.attr("x", '0%').attr("y", util.correct(margins.top)+'%')
				.attr('height', util.correct(100-margins.bottom-margins.top)+'%').attr('width', '100%');
		};
		return obj;
	};
}]);
