angular.module('hsGraph').directive('hsLegend', ['hsGraphUtil', 'HsLegend', function(util, HsLegend) {
	return {
		restrict: 'EA',
		replace: true,
		require: '^hsGraph',
		template: '<svg class="hs-legend"><svg><rect x="0%" y="0%" width="100%" height="100%"></svg></svg>',
		link: function(scope, ele, attrs, ctrl) {
		    var legend = new HsLegend(util.d3.select(ele[0]));
			ctrl.addComponent(legend);
            if (attrs.xpos && (attrs.xpos !== "")) { legend.cfg().x = parseInt(attrs.xpos); }
            if (attrs.ypos && (attrs.ypos !== "")) { legend.cfg().y = parseInt(attrs.ypos); }
            if (attrs.labels) {
                legend.cfg().labels = JSON.parse(attrs.labels);
            }
		}
	};
}]);

