angular.module('hsGraph').factory('HsSeries', ['HsComponent', 'HsLabels', 'HsMarkers', 'hsGraphUtil', '$compile', function HsSeriesFactory(HsComponent, HsLabels, HsMarkers, util, $compile) {    
    "use strict";    

    function initObj(obj) {
        obj.lineGroup   = obj.cfg().elem.append('svg').attr('class', 'hs-series-line-group')
            .attr('x', '0%').attr('y', '0%').attr('width', '100%').attr('height', '100%');
        obj.areaGroup   = obj.cfg().elem.append('svg').attr('class', 'hs-series-area-group')
            .attr('x', '0%').attr('y', '0%').attr('width', '100%').attr('height', '100%');
        obj.markerGroup = obj.cfg().elem.append('svg').attr('class', 'hs-series-marker-group')
            .attr('x', '0%').attr('y', '0%').attr('width', '100%').attr('height', '100%');
        obj.labelGroup  = obj.cfg().elem.append('svg').attr('class', 'hs-series-label-group')
            .attr('x', '0%').attr('y', '0%').attr('width', '100%').attr('height', '100%');
            
        obj.labels = new HsLabels(obj.labelGroup, "Series Labels", obj).cfg({
            cssClass: "hs-series-label",
            anchor: 'middle',
            baseline: 'auto',
            dy: '-6px'
        });
        
        obj.markers = new HsMarkers(obj.markerGroup, "Series Markers", obj).cfg({
            cssClass: 'hs-series-marker',
            style: 'circle', size: 8, visible: true,
            gradient: { angle: -90, stops: [
                {offset:'0%', k:0.3}, 
                {offset:'80%', k:0}, 
                {offset:'100%', k:0.15}
            ]}
        });
    }
    
    
    function drawAreas(obj) {
        return function(coord) {
            if (obj.cfg().area.visible) {
                var x = obj.data.indices.x, y = obj.data.indices.y;
                var aux = obj.data.aux;
                obj.areaGroup.attr('viewBox', '0 0 100 100').attr('preserveAspectRatio', 'none');
                var values = obj.data(); 
                var area = util.d3.svg.area()
                    .x(function(d) { 
                        return coord.x.get(d[x]); })
                    .y0(function(d, i) { 
                        return coord.y.get(aux? aux[i].y0:0); })
                    .y1(function(d,i) { 
                        return coord.y.get(aux? aux[i].y:d[y], aux? aux[i].y0:0); })
                    .interpolate("linear");
                var areas = obj.areaGroup.selectAll('.hs-series-area').data([values]);
                areas.enter().append('path').attr('class', 'hs-series-area').attr('stroke-width','0')
                    .append("title").text(function() { return util.getTooltip(obj); }); 
                areas.transition().duration(obj.cfg().transition)
                    .attr('d', function(d) { 
                        return area(d); 
                     });
                areas.exit().remove();
                obj.areaGroup.gradient(areas, (obj.cfg().compName || '') + 'AreaGrd');
            }
        };
    }
    
    function drawLines(obj) {
        return function(coord) {
            if (obj.cfg().lines.visible) {
                var x = obj.data.indices.x, y = obj.data.indices.y;
                var values = obj.data(); 
                var aux = obj.data.aux;
                var lines  = obj.lineGroup.selectAll('.hs-series-line').data(values);
                lines.enter().append('line').attr('class', 'hs-series-line')
                    .attr("tooltip-append-to-body", true)
                    .attr("tooltip-placement", "top-right")                    
                    .attr('uib-tooltip', function(d) { return util.getTooltip(obj, d, true); });                    
                lines.exit().remove();
                lines.transition().duration(obj.cfg().transition)
                    .attr('x1', function(d,i) {
                        var dx = obj.data.query(Math.max(0,i-1))(x)[0];
                        return (coord.x.get(dx))+'%'; })
                    .attr('y1', function(d,i) { 
                        var j = Math.max(0,i-1);
                        return (coord.y.get(aux?aux[j].y:obj.data.query(j)(y)[0], aux?aux[j].y0:0)) +'%'; })
                    .attr('x2', function(d)   { 
                        return (coord.x.get(d[x])) +'%'; })
                    .attr('y2', function(d,i)   { 
                        return (coord.y.get(aux?aux[i].y:d[y], aux?aux[i].y0:0)) +'%'; });
            $compile($(obj.lineGroup[0]))(this.myScope); 
            }
        };
    }

    function drawLabels(obj) {
        return function(coord) {
            var aux = obj.data.aux;
            var x = obj.data.indices.x, y = obj.data.indices.y, l = obj.data.indices.label;
            function xpos(d) { return coord.x.get(d[x]) + '%'; }
            function ypos(d, i) { return coord.y.get(aux?aux[i].y:d[y], aux?aux[i].y0:0) + '%';}
            obj.labels.strings = obj.data(); 
            obj.labels.draw([xpos, ypos], 'ordinal', function labelFn(d) { return d[l]; });
        };
    }
    
    function drawMarkers(obj) {
        return function(coord) {
            var aux = obj.data.aux;
            var x = obj.data.indices.x, y = obj.data.indices.y;
            function xpos(d) { return (coord.x.get(d[x])) + '%'; }
            function ypos(d, i) { return (coord.y.get(aux?aux[i].y:d[y], aux?aux[i].y0:0)) + '%';}
            function tooltip(d) { return util.getTooltip(obj, d, true); }
            obj.markers.draw(obj.data(), {xpos:xpos, ypos:ypos}, tooltip);
            $compile($(obj.markerGroup[0]))(this.myScope);  
        };
    }
    
    function getCoords(axis, offset, noScale) {
        var scale = axis.scale;
                  
        /*
         * don't correct results
         */
        function getDateRaw(v, v0) {
            var _v = (v0)? new Date(v + v0) : new Date(v);
            var result = scale(_v) + offset;
            if (isNaN(result)) { 
                console.log('nan for v=' + v + ' and v0=' + v0); }
            return result;
        }
        
        function getDate(v, v0) {
           return util.correct(getDateRaw(v, v0));
        }
        
        /*
         * don't correct results
         */
        function getCoordRaw(v, v0) {
            var _v = v + (v0||0);
            var result = scale(_v) + offset;
            if (isNaN(result)) { 
                console.log('nan for v=' + v + ' and v0=' + v0); }
            return result;
        }
        
        function getCoord(v, v0) {
           return util.correct(getCoordRaw(v, v0));
        }
        
        function getOrdRaw(v) {
            v = ' '+v+' ';
            var result = scale(v);
            if (isNaN(result)) { 
                console.log('nan for ' + v); }
            return result;
        }

        function getOrd(v) {
            return getOrdRaw(v) + offset;
        }
        
        function getRangeScale(v) {
            var range = axis.getRangeExtent();
            v = parseFloat(v);
            var w = (v * (range[1]-range[0]) + range[0]);
            return w;
        }

        var result = {};
        if (noScale) { result.get = result.getRaw = getRangeScale; }
        else { 
            switch(axis.cfg().scaleType) {
                case 'ordinal': result.get = getOrd; result.getRaw = getOrdRaw; break;
                case 'date':    result.get = getDate; result.getRaw = getDateRaw; break;
                default:        result.get = getCoord; result.getRaw = getCoordRaw;
            }
        }  
        return result;
    }
    
    function initGradients(obj) {
        if (obj.markers.cfg().gradient) {
            if (!obj.markerGroup.gradient) {
                obj.markerGroup.gradient = util.gradient(obj.markers.cfg().gradient);
            }
        } else {
            obj.markerGroup.gradient = function() {};    // dummy function
        }
        if (obj.cfg().area.gradient) {
            if (!obj.areaGroup.gradient) {
                obj.areaGroup.gradient = util.gradient(obj.cfg().area.gradient);
            }
        } else {
            obj.areaGroup.gradient = function() {};      // dummy function
        }
    }
    
    function addToStack(obj, series) {
        series.stacked.offset = obj.cfg().shared?'expand' : 'zero';    
        obj.data.aux = [];
//        var org = {indices: obj.data.indices, values: obj.data.values()};
        var org = {indices: obj.data.indices, values: obj.data()};
        for (var r=0; r<org.values.length; r++) {
            obj.data.aux[r] = {org:org};
        }     
        series.stacked.push(obj.data.aux);
    }
    
    return function(elem, seriesType, name) {
        var obj = new HsComponent('series', elem, name).cfg({
                lines: {visible: true },
                area: { visible: false,
                        gradient: { angle: -90, stops: [
                            {offset:'0%', k:0, opacity:0.75 },
                            {offset:'100%', k:0, opacity:0.25 }
                        ]}
                },
                seriesType: seriesType
            });
        initObj(obj);

        obj.init = function(allSeries) {            
            // collect bar series that share the vertical bar categories  
            if (obj.collectBarSet) { obj.collectBarSet(allSeries); }
            
            // prepare series that are stacked or shared
            if (obj.cfg().stacked || obj.cfg().shared) { addToStack(obj, allSeries); }            
        };

        obj.setSize = function(chartMargin, xaxis, yaxis) {
            obj.xaxis = xaxis;
            obj.yaxis = yaxis;            
        };
        
        obj.setStackDomain = function(dom) {
            obj.data.colDomain(obj.data.indices.y, dom);
        };
        
        obj.draw = function draw() {
            if (!obj.cfg().visible || !obj.data.indices) { return; }
            initGradients(obj);
            var xoffset = (obj.xaxis.cfg().scaleType === 'ordinal')? (obj.xaxis.getRangeBand()/2) : 0;
            var yoffset = (obj.yaxis.cfg().scaleType === 'ordinal')? (obj.yaxis.getRangeBand()/2) : 0;
            var coord = {
                x:getCoords(obj.xaxis, xoffset, obj.cfg().noScaleX), 
                y:getCoords(obj.yaxis, yoffset, obj.cfg().noScaleY)
            };
   
            obj.drawAreas(coord);
            obj.drawLines(coord);
            obj.drawMarkers(coord);
            obj.drawLabels(coord);
        };        
        obj.drawAreas   = drawAreas(obj);
        obj.drawLines   = drawLines(obj);
        obj.drawMarkers = drawMarkers(obj);      
        obj.drawLabels  = drawLabels(obj);
        return obj;
    };
}]);

