angular.module('hsGraph').directive('hsChart', ['HsChart', 'hsGraphUtil', function(HsChart, util) {
	return {
		restrict: 'EA',
		replace: true,
		require: '^hsGraph',
		template: '<svg class="hs-chart"><rect x="0%" y="0%" width="100%" height="100%"></rect></svg>',
		link: function(scope, ele, attrs, ctrl) {
			var comp = new HsChart(util.d3.select(ele[0]));
			ctrl.addComponent(comp);
//			if (attrs.title) { comp.cfg().title = attrs.title; }
		}
	};
}]);

