angular.module('hsGraph').directive('hsGraph', ['hsGraphUtil', 'HsGraph', function(util, HsGraph) {
	return {
		restrict: 'EA',
		controller: 'hsGraphController',
		transclude: true,
		replace: true,
		template: '<svg class="hs-graph" width="100%" height="100%">' + 
		             '<defs></defs>' +
					 '<rect width="100%" height="100%" x="0%" y="0%"></rect>' +
					 '<svg class="hs-graph-header" width="100%" height="10%" x="0%" y="0%">' +
						'<rect width="100%" height="100%"></rect>' +
					 '</svg>' +
				  	 '<svg class="hs-graph-body" width="100%" height="90%" x="0%" y="10%" ng-transclude></svg>' +
				  '</svg>',
		link: function(scope, ele, attrs, ctrl) {
			var comp = new HsGraph(ele);
			if (attrs.titleLink) { comp.cfg().chartTitleLink = attrs.titleLink; }
			if (attrs.title) { comp.cfg().chartTitle = attrs.title; }
			else { 
                comp.cfg().elem.selectAll('.hs-graph-header').attr('height', '0%'); 
                comp.cfg().elem.selectAll('.hs-graph-body').attr('height', '100%').attr('y', '0%'); 
			}
			ctrl.addComponent(comp);
			ctrl.getData(attrs.source); 
		}
	};
}]);

