angular.module('hsGraph').directive('hsSeries', ['hsGraphUtil', 'hsDataType', 'HsVBarSeries', 'HsHBarSeries', 'HsAreaSeries', 'HsLineSeries', 'HsScatterSeries', 
    function(util, dataType, HsVBarSeries, HsHBarSeries, HsAreaSeries, HsLineSeries, HsScatterSeries) {
	"use strict";
	return {
		restrict: 'EA',
		replace: true,
		require: '^hsGraph',
		template: '<svg class="hs-series"></svg>',
		link: function(scope, ele, attrs, ctrl) {
    			var series;
                // fetch type
                var type = attrs.type || 'scatter';
                if (attrs.line === "")      { type = 'line'; }
                else if (attrs.vbar === "") { type = 'vbar'; }
                else if (attrs.area === "") { type = 'area'; }
                else if (attrs.hbar === "") { type = 'hbar'; }
                switch (type) {
                    case 'area':    series = new HsAreaSeries(util.d3.select(ele[0])); break;
                    case 'vbar':    series = new HsVBarSeries(util.d3.select(ele[0])); break;
                    case 'hbar':    series = new HsHBarSeries(util.d3.select(ele[0])); break;
                    case 'line':    series = new HsLineSeries(util.d3.select(ele[0])); break;
                    case 'scatter': series = new HsScatterSeries(util.d3.select(ele[0])); break;
                    default:        series = new HsLineSeries(util.d3.select(ele[0])); break;
                }
                                
                // capture the scope to allow $compile calls; used for adding tooltip to markers with AngularUI. 
                series.myScope = scope;                        
                
                // fetch cumulated
                series.cfg().cumulated = (attrs.cumulated === "");

                // fetch stacked
                series.cfg().shared = (attrs.shared === "");
                series.cfg().stacked = (attrs.stacked === "");
                if (series.cfg().stacked) { series.cfg().shared = false; }

    			// fetch marker config
    			if (attrs.markers) {
    				var items = attrs.markers.split(' ');
    				for (var i in items) {
    					if (isNaN(items[i])) { series.markers.cfg().style = items[i]; }
    									else { series.markers.cfg().size = items[i]; }
    				}
    			}
    			
                // fetch noScale
                if (attrs.noscalex !== undefined) { series.cfg().noScaleX = true; }
                if (attrs.noscaley !== undefined) { series.cfg().noScaleY = true; }

    			function setKeys(at, xy) {
                    try { 
                        var v = JSON.parse(at); 
                        if (v instanceof Array) { 
                            for (var i=0; i<v.length; i++) { xy[i] = dataType.castData(dataType.getType(v[i]), v[i]); }
//                            for (var i=0; i<v.length; i++) { xy[i] = noScale? v[i] : dataType.castData(dataType.getType(v[i]), v[i]); }
//                            for (var i=0; i<v.length; i++) { xy[i] = v[i]; }
                        } 
//                        else { xy[0] = noScale? v : dataType.castData(dataType.getType(v), v); }
                        else { xy[0] = v; }
                    } 
                    catch(e) { xy[0] = at; }
    			}

    			// fetch xval, yval, label as keys or values
    			series.keys = { x: [],  y: [], labels: "", colors: ""};
                if (attrs.label) { series.keys.labels = attrs.label; }
                if (attrs.tip)   { series.keys.tip = attrs.tip; }
                if (attrs.color) { series.keys.colors = attrs.color; }
                if (attrs.xval==='now') {
                    series.keys.x[0] = new Date();
                    if (series.keys.y.length === 0) { series.keys.y = [0, 1]; }
                    series.setName('now');
                    series.cfg().noScaleY = true;
                } else {
                    setKeys(attrs.xval, series.keys.x);
                    setKeys(attrs.yval, series.keys.y);
                }
    			    			
    			// fetch name
    			if (attrs.name) {
                    series.setName(attrs.name);
    			}
    			
    			// and finally, add the component
    			ctrl.addComponent(series);
		}
	}; 
}]);


