angular.module('hsGraph').factory('HsMarkers', ['HsComponent', function HsMarkersFactory(HsComponent) {  
    function setMarkerShape(cfg) {
        return function(m) {
            var shape = 'rect';
            var angle = 0;
            switch (cfg.style) {
                case 'diamond': shape = 'rect';     angle = 45; break;
                case 'circle':  shape = 'circle';   angle = 0;  break;
                case 'square':
                /* falls through */
                default:        shape = 'rect';     angle = 0;
            }            
            var cnt = m.append('svg');
            var shapes = cnt.append(shape);
            if (cfg.cssClass2) { shapes.attr('class', cfg.cssClass2); }
            if (!cfg.unit) { cfg.unit = ''; }
            
            if (cfg.size instanceof Array) { for (var i=0; i<cfg.size.length; i++) { cfg.size[i] = parseInt(cfg.size[i]); }}
                                      else { cfg.size = parseInt(cfg.size); }
            var r = cfg.size.length? [cfg.size[0], cfg.size[1]] : [cfg.size, cfg.size];
            if (shape === 'circle') { shapes.attr('r', cfg.size/2+cfg.unit); }
                               else { shapes.attr('width', r[0]+cfg.unit).attr('height', r[1]+cfg.unit); }
            
            if (cfg.style === 'diamond') {
                cnt.attr('x', '0'); cnt.attr('y', '-'+r[1]/Math.sqrt(2)+cfg.unit);
                shapes.attr('transform', 'rotate(' + angle + ')');
            } else if (cfg.style !== 'circle') {
                cnt.attr('x', '-'+r[0]/2+cfg.unit); cnt.attr('y', '-'+r[1]/2+cfg.unit);
            }
        };
    }

    return function(elem, name, parent) { 
        var obj = new HsComponent('markers', elem, name, parent);

        obj.draw = function draw(data, pos, tooltip, cfgFn) {
            var m;
            if (obj.cfg().visible) {
                var cssClass = obj.cfg('cssClass');
                var markers = elem.selectAll("."+cssClass).data(data);
                if (pos.width) {     // bar series marker
                    var color = pos.colorClass || function() { return ''; };

                    markers.enter().append("rect").attr('class', function(d) { return cssClass+' '+color(d); });
                    markers.transition().duration(obj.cfg().transition) 
                        .attr('x', pos.xpos).attr('y', pos.ypos)                   
                        .attr('width', pos.width).attr('height', pos.height);                    
                    markers.attr('overflow', 'visible')
                        .attr("tooltip-append-to-body", true)
                    	.attr("tooltip-placement", 'auto top')
                        .attr('uib-tooltip', tooltip);                       
                } else {
                    m = markers.enter().append("svg")
                        .attr('overflow', 'visible')
                        .attr("tooltip-append-to-body", true)
                    	.attr("tooltip-placement", 'auto left')
                        .attr('uib-tooltip', tooltip);                       
                    if (cfgFn) {     // legend marker
                       m.attr('class', function(d) { return cfgFn(d).cssClass; })
                        .each(function (d) { return setMarkerShape(cfgFn(d))(d3.select(this)); })
                        .transition().duration(obj.cfg().transition).attr('x', pos.xpos).attr('y', pos.ypos);                    
                    } else {        // series marker
                       m.attr('class', obj.cfg().cssClass).call(setMarkerShape(obj.cfg()));
                       markers.transition().duration(obj.cfg().transition).attr('x', pos.xpos).attr('y', pos.ypos);                    
                    }                   
                }
                markers.exit().remove();              
            }
        };
        return obj;
    };
}]);
