angular.module('hsGraph').factory('HsXAxis', ['HsAxis', 'hsGraphUtil', function(HsAxis, util) {
    return function(elem, primary) {
        var obj = new HsAxis(true, elem, primary, 'X Axis');

        var axisLabelCfg = {
            pos     : '10%',
            dy      : '1em',
            anchor  : 'middle'
        };
        obj.axisMajorLabels.cfg(axisLabelCfg).cfg().limitLabel = true;
        obj.axisMinorLabels.cfg(axisLabelCfg);

        obj.setSize = function(margins) {
            obj.requestRange([margins.left, 100-margins.right]);
            if (obj.cfg().elem) { obj.cfg().elem.transition().duration(obj.cfg().transition)
                    .attr("x", '0%').attr("y", util.correct(100-margins.bottom)+'%')
                    .attr('height', util.correct(margins.bottom)+'%').attr('width', '100%');
            }
            obj.getTickMarks();
        };
        return obj;
    };
}]);

