angular.module('hsGraph').factory('HsScatterSeries', ['HsSeries', 'hsGraphUtil', function(HsSeries) {
    "use strict";    
    return function(elem) {
        var obj = new HsSeries(elem, 'scatter', 'Scatter Series');
        obj.cfg().lines.visible = false;
        obj.cfg().area.visible = false;
        obj.markers.cfg().visible = true;
        return obj;
    };
}]);

