angular.module('hsGraph').controller('hsGraphController', 
    ['$scope', '$http', 'HsData', 'HsXAxis', 'HsYAxis', 'hsDataType', function HsGraphController($scope, $http, HsData, HsXAxis, HsYAxis, dataType) {
	"use strict";

	function drawComponents(cc) {
        cc.graph[0].setTextSize(function() {
    	    cc.graph[0].init(cc);
            if ($scope.setParameters) { $scope.setParameters(cc); } // call to client controller to manipulate settings
            cc.graph[0].draw(cc);
        });
	}
	
	$scope.data = null;
    $scope.components = [];
    $scope.components.xaxis = [];
    $scope.components.yaxis = [];
    $scope.components.hgrid = [];
    $scope.components.vgrid = [];
    $scope.components.graph = [];
    $scope.components.chart = [];
    $scope.components.series = [];
    $scope.components.xaxis = [];
    $scope.components.legend = [];
    $scope.components.panelSize = function() { return $scope.components.graph[0].size(); };
    
//    $scope.loadData         = $scope.loadData || hsDataLoader.loadData;
    $scope.preprocessData   = $scope.preprocessData || function() {};
    $scope.postprocessData  = $scope.postprocessData || function() {};

	$scope.$on('hsTabChange', function(/*event*/) {
		var cc = $scope.components;
		var elem = $(cc.graph[0].cfg().elem[0][0]);
		setTimeout(function() {
			if (elem.is(':visible')) { drawComponents(cc); }
		}, 0);
	});
	
	this.getData = function(source) {
		new HsData(source).load($http).then(draw);
 	};

	/**
	 * draw function, called by this controller once the data set is available
	 */
	function draw(hsData) {
		var cc = $scope.components;
		if (!hsData) { throw 'data undefined'; }
		
		// add hidden axes if non were specified
		if (cc.xaxis.length === 0) { addComponent(new HsXAxis(null, true).visible(false)); }
		if (cc.yaxis.length === 0) { addComponent(new HsYAxis(null, true).visible(false)); }

        $scope.preprocessData(hsData);
        cc.series.data = hsData;                  // already a copy of the original file data
        for (var i=0; i<cc.series.length; i++) { 
            cc.series[i].data = hsData.copy();    // each series gets a shallow copy
        }
		setSeriesData(cc);
		$scope.postprocessData(cc);	// call to client controller to manipulate data
        checkCumulated(cc);
        drawComponents(cc);
	}

	function checkCumulated(cc) {
        var sum;
        function accum(y) { return function(v, i, data) { sum+= data[i][y]; return sum; };}
        for (var i=0; i<cc.series.length; i++) { 
            var s = cc.series[i];
            if (s.cfg('cumulated')) {
                var y = s.data.indices.y;
                var name = s.data.colName(y);
                sum=0;
                s.data.indices.y = s.data.newColumn(name+'_Cum', accum(y));
            }
        }
	}

    function addComponent(component) {
        var type = component.getType();
        if (!type) { console.log('no component type set'); }
        else {
            // if new component is a graph: add all known components to it
            if (type === 'graph') {
                for (var c=0; c<$scope.components.length; c++) { component.children($scope.components[c]); }
            }
            
            // if a graph component is already defined, add new component to it
            var cat = $scope.components.graph;
            if (cat.length > 0) { cat[0].children(component); }

            // in addition: add component to $scope
            $scope.components.push(component);
            cat = $scope.components[type];
            if (!cat) { cat = $scope.components[type] = []; }
            cat.push(component);
            if (component.cfg().compName) { cat[component.cfg().compName] = component; }            
            }
    }


  	
/**
 * Determine if x is a key, i.e. starts with a letter
 * @param {Object} x
 */
	function isKey(x) {
	    if (!isNaN(x)) { return false; }           // literal number
	    var letter = x.charAt(0).toUpperCase();    // else check for literal string
	    return (letter.toLowerCase() !== letter);  // -> if not equal, starts with a letter -> is a key
	}
    
    // determines a preferred data type to cast to given an explicit axis scale
    function setScaleType(axisScale, data, index) {
        var dataScale = data.colType(index);
        switch (axisScale) { 
            case 'linear':
            case 'log':			
            case 'percent':		if (dataScale!=='real' && dataScale!=='percent') { data.colType(index, 'real'); }
            					break;
            case 'index':  		if (dataScale!=='real') { data.colType(index, 'real'); }
            					break;
            case 'ordinal':  	
            case 'nominal':  	if (dataScale!=='nominal') { data.colType(index, 'nominal'); } 
            					break;
            case 'date': 		if (dataScale!=='date') { data.colType(index, 'date'); } 
            					break;
            default:    		if (dataScale===undefined) { data.colType(index, 'real'); } 
        }
    }
    
    // determines a default axis scale for the provided data scale
    function getAxisScaleType(data, key, k) {
        switch (key? data.colType(k) : dataType.getType(k)) {
        	case 'real':     return 'linear';
        	case 'percent':  return 'linear';
            case 'nominal':  return 'ordinal';
            case 'date':     return 'date';
            default:         return 'linear';
        }
    }
    
    function constant(val) {
        var r = parseFloat(val);
        if (typeof val==='string' && val.indexOf('%')===val.length-1) {
            r /= 100.0;
        } 
        return function() { return r; };
    }

    function setSeriesData(cc) {
        var index;
        function sortAscendingX(a, b) { return a[index.x] - b[index.x]; }

        function removeEmpty(index) {
            return function(sample) {
                return (sample[index] !== '' && sample[index] !== '#REF!' && sample[index] !== null);
            };
        }

        for (var i=0; i<cc.series.length; i++) {
            var s = cc.series[i];
            var data  = s.data;        

            // record the semantic indices used in the new array
            var j;
            var xk = s.keys.x,  yk = s.keys.y,  // each an array of keys for x resp. y value
                keyx = [],      keyy = [],      // each an array of boolean; true if xk[i]/yk[i] are keys, false if value literals
                lk = s.keys.labels,
                ck = s.keys.colors;
                
            index = s.data.indices = {x:0, y:0};

                // determine if references are literal (e.g. '12') or keys (e.g. 'Value')
            for (j=0; j<xk.length; j++) { 
                keyx[j] = (isKey(xk[j]) && (data.colName(xk[j])!==undefined)); // -> a valid key  
            }
            for (j=0; j<yk.length; j++) { 
                keyy[j] = (isKey(yk[j]) && (data.colName(yk[j])!==undefined)); // -> a valid key  
            }
            
            index.label = index.labelOrg = data.colNumber(lk);
            index.color =                  data.colNumber(ck);
            index.x2    =                  data.colNumber(xk[1]);
            index.y2    =                  data.colNumber(yk[1]);

            var aux;
            var xkyk = (yk.length === xk.length);
            if (xk.length>1) {               // multiple x keys provided:
                if (!keyx[0] && !keyx[1]) {  // if 2 x-literals: create a literal series
                    aux = [];
                    index.x = 0;  index.y = 1;
                    for (j=0; j<xk.length; j++) { aux.push([xk[j],  xkyk?yk[j]:yk[0]]); }
                    s.data.data(aux, ['x', 'y']);
                } else if (keyx[0] && keyx[1]) { // 2 x-keys: from-to; y-key must be key
                    index.x = index.xOrg = data.colNumber(xk[0]);
                    index.y = index.yOrg = data.colNumber(yk[0]); 
                } else {     // mix of literals an keys: not supported
                    console.log('case x.dim=' + xk.length + ', ydim=' + yk.length + ' not implemented');
                }
            } else if (yk.length>1) {        // 1 x-dim, 2 y-dimensions: from-to 
                if (!keyy[0] && !keyy[1]) {  // if 2 y-literals: create a literal series
                    aux = [];
                    index.x = 0;  index.y = 1;
                    for (j=0; j<yk.length; j++) { aux.push([xkyk?xk[j]:xk[0],  yk[j]]); }
                    s.data.data(aux, ['x', 'y']);
                } else if (keyy[0] && keyy[1]) { // 2 y-keys: from-to; x-key must be key
                    index.x = index.xOrg = data.colNumber(xk[0]);
                    index.y = index.yOrg = data.colNumber(yk[0]); 
               } else {     // mix of literals an keys: not supported
                    console.log('case x.dim=' + xk.length + ', ydim=' + yk.length + ' not implemented');
                }
            } else {         // standard case: 1D in x and in y, key or literal
                if (keyx[0] && keyy[0]) {       // x and y both keys: we're all set
                    index.x = index.xOrg = data.colNumber(xk[0]);
                    index.y = index.yOrg = data.colNumber(yk[0]); 
                } else if (keyx[0]) {           // x key and y literal:
                    index.x = index.xOrg = data.colNumber(xk[0]);
                    index.y = index.yOrg = s.data.newColumn('yConst', constant(yk[0]));
                } else if (keyy[0]) {           // y key and x literal:
                    index.x = index.xOrg = s.data.newColumn('xConst', constant(xk[0]));
                    index.y = index.yOrg = data.colNumber(yk[0]); 
                } else {                        // x and y literal:
                    throw new Error('single point literals not yet supported');
                }
            }

            // filter any rows that contain NaNs or empty x or y fields
            s.data.filter(removeEmpty(index.x));
            s.data.filter(removeEmpty(index.y));
            if (s.data().length === 0) { s.visible(false); }

            if (cc.xaxis[0].cfg().scaleType === undefined) { 
                cc.xaxis[0].cfg().scaleType = getAxisScaleType(data, keyx[0], xk[0]); 
            } else {
                if (!s.cfg().noScaleX) { setScaleType(cc.xaxis[0].cfg().scaleType, data, index.x); }
            }
            if (cc.yaxis[0].cfg().scaleType === undefined) { 
                cc.yaxis[0].cfg().scaleType = getAxisScaleType(data, keyy[0], yk[0]); 
            } else {
                if (!s.cfg().noScaleY) { setScaleType(cc.yaxis[0].cfg().scaleType, data, index.y); }
            }
            
            s.data.cast();
            switch (s.cfg().seriesType) {
                case 'area': 
                case 'line': s.data.sort(sortAscendingX); break;  // sort by x values
                default:     break;
            }
        }
    }

    this.addComponent = addComponent;
    $scope.$on('hs-resize-begin', function() { });
    $scope.$on('hs-resize-end', function() {
        var cc = $scope.components;
            cc.graph[0].draw(cc);
    });

}]);

