angular.module('hsGraph').factory('HsComponent', ['HsConfigurable', function HsComponentFactory(HsConfigurable) {
	return function(type, elem, name, parent) {
	    if (!name) { console.log('name missing for ' + type); }
		var obj = new HsConfigurable(type, name, parent).cfg({
		    visible: true,
			elem: elem,
			transition: 400	// ms
		});
		obj.visible = function visible(yes) { 
		    if (yes===undefined) { return obj.cfg().visible; } 
		    else { 
		        obj.cfg().visible = yes;
		        return obj; 
		    }
		};
		obj.setName = function(n)	   {
			obj.cfg().compName = n;
            n = n.replace(/ /g, '-');
            obj.cfg().class = n;
			elem.classed(n, true);
			return obj;
		};
		obj.getName = function() { return obj.cfg().compName; };
		
		return obj;
	};
}]);

